<template>
	<div v-if="song" class="box">
		<div class="song-title">{{song.title}}</div>
		<div class="center" @click="playSong">
			<div class="bor837F7D">
				<img :style="{transform: rotate}" class="cover" :src="song.coverUrl" alt="">
				<img :style="{opacity: opacity}" class="play" src="../assets/imgs/icon/play.png" alt="">
			</div>
		</div>
		<div class="range">
			<div v-if="isThis" :style="{width: playtime}" class="had-line"></div>
		</div>
		<div class="text"><p v-html="texts"></p></div>
		<!-- <audio-box></audio-box> -->
	</div>
</template>

<script>
export default {
	name: 'play',
	props: {
		listdata: Array,
		isThis: null,
		playtime: String,
	},
	data () {
		return {
			song: null,
			audio: null,
			isPlay: false,
			opacity: 1,
			rotate: 'rotate(0deg)',
			imgRotate: null,
			rotateNum: 0,
		}
	},
	computed: {
		texts : function(){
			return this.song.description.replace(/\n/g, '<br/>')
		}
	},
	watch: {
		isThis: function(data){
			if(data){
				this.isPlay = true;
				this.opacity = 0;
				this.imgRotateStart();		
			}else{
				this.isPlay = false;
				this.opacity = 1;
				this.imgRotateStop();
			}
		}
	},
	mounted () {
		this.song = this.listdata[this.$route.params.data];
		this.$emit('loadurl' ,this.song.fileOptions[1].url)
		if(this.isThis){
			this.isPlay = true;
			this.opacity = 0;
			this.imgRotateStart();		
		}
	},
	methods: {
		playSong: function(){
			this.isPlay = !this.isPlay;
			this.$emit('playbtn')
			if(this.isPlay){
				this.playBtnHide();
				this.imgRotateStart();		
			}else{
				this.playBtnShow();
				this.imgRotateStop();
			}
		},
		playBtnHide: function(){
			var that = this;
			var t = setInterval(function(){
				that.opacity -= 0.1
				if(that.opacity<=0){
					clearInterval(t);
				}
			},20)
		},
		playBtnShow: function(){
			var that = this;
			var t = setInterval(function(){
				that.opacity += 0.1
				if(that.opacity>=1){
					clearInterval(t);
				}
			},20)
		},
		imgRotateStart: function(){
			var that = this;
			if(this.imgRotate){return}
			this.imgRotate = setInterval(function(){
				that.rotateNum += 0.1;
				that.rotate = 'rotate(' + that.rotateNum + 'deg)';
			},10)
		},
		imgRotateStop: function(){
			clearInterval(this.imgRotate)
			this.imgRotate = null;
		}
	}
}
</script>

<style scoped>
.box{
	width: 100%;
}
.song-title{
	width: 100%;
	height: 2.5rem;
	line-height: 2.5rem;
	text-align: center;
	font-size: 1.5rem;
	margin-top: 2rem
}
.center{
	width: 100%;
	height: 13rem;
	padding-top: 0.5rem
}
.bor837F7D{
	width: 12.5rem;
	height: 12.5rem;
	border-radius: 50%;
	background: #000;
	margin: 0 auto;
	position: relative;
	border: 0.25rem solid #837F7D;
}
.cover{
	width: 9rem;
	height: 9rem;
	display: block;
	margin: 1.5rem auto;
	border-radius: 50%
}
.play{
	width: 4rem;
	height: 4rem;
	position: absolute;
	left: 4rem;
	top: 4rem;
}
.range{
	width: 80%;
	height: 0.1rem;
	margin: 3rem auto;
	background: #f0f0f0;
}
.had-line{
	width: 0%;
	height: 0.1rem;
	float: left;
	background: #d00
}
.text{
	width: 80%;
	line-height: 1.1rem;
	font-size: 1rem;
	color: #333;
	margin: 0 auto;
}
.hide{
	display: none;
}
.show{
	display: block
}
</style>


