<template>
  <div id="app">
    <header-box></header-box>
    <router-view :playtime="playTime" :isThis="isThis" v-on:loadurl="loadSongUrl" :listdata="listData" v-on:playbtn="playSong" />
    <audio-tem v-show="false" :playurl="playUrl" :isplay="isPlay" v-on:playtime="playTimeFun"></audio-tem> 
  </div>
</template>

<script>
import Header from './components/Header'
import AudioTem from './components/AudioTem'
export default {
    name: 'App',
    data () {
        return {
            audio: 'audiooo',
            listData: [],
            isPlay: false,
            isThis: false,
            songUrl: null,
            playUrl: null,
            playTime: '0%',
        }
    },
    components: {
        headerBox: Header,
        audioTem: AudioTem
    },
    watch: {
    },
    mounted () {
        this.getData();
    },
    methods: {
        getData: function(){
            this.$http.get('../../static/data/xzq.json').then((res)=>{
                this.listData = res.body.data
            })
        },
        loadSongUrl: function(data){
            this.songUrl = data;
            if(!this.playUrl){
                this.playUrl = data;
            }else if(this.playUrl == this.songUrl && this.isPlay){
                this.isThis = true;
            }else{
                this.isThis = false;
            }
        },
        playSong: function(){
            var that = this;
            if(this.playUrl != this.songUrl){
                this.playUrl = this.songUrl;
                this.isPlay = false;
            }
            //添加延迟，使监听可以监听到变化，同时也使audio有加载时间
            setTimeout(function(){
                that.isPlay = !that.isPlay;
            },100)
            //恢复默认
            this.isThis = true;
            this.playTime = '0%';
        },
        playTimeFun: function(data){
            this.playTime = data;
        }
    }
}
</script>

<style>
@font-face{
	font-family: myFirstFont;
	src: url('assets/ttf/fzkai.ttf')
}
html,body{
	font-size: 20px
}
#app {
  font-family: "Times New Roman",myFirstFont;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  padding-bottom: 3rem;
}
</style>



