<template>
<div>
	<div class="list" v-for="(item, index) in listdata">
		<router-link :to="{name: 'play', params: {data: index}}">
			<div class="left-img"><img :src="item.coverUrl" alt=""></div>
			<div class="right-text">
				<p class="title">{{item.title}}</p>
				<p class="text">{{item.description}}</p>
			</div>
		</router-link>
	</div>
</div>
</template>

<script>
export default {
	name: 'Index',
	props: {
		listdata: Array
	},
	data () {
		return {
		}
	},	
}
</script>

<style scoped>
.list{
	width: 100%;
	height: 5rem;
	padding: 0.5rem 5%;
}
.left-img{
	width: 4rem;
	height: 4rem;
	float: left;
}
.left-img>img{
	width: 100%;
	height: 100%
}
.right-text{
	width: 100%;
	height: 100%;
	padding-left: 4.5rem;
}
.title{
	width: 100%;
	color: #333;
	font-weight: 700;
	line-height: 1rem;
	font-size: 1rem;
}
.text{
	width: 100%;
	color: #444;
	font-size: 0.8rem;
	line-height: 0.9rem;
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 3;
	overflow: hidden;
	margin-top: 0.5rem;
}
</style>


