<template>
	<div class="box">
		<audio ref="audio" controls loop :src="playurl"></audio>
	</div>
</template>

<script>
export default {
	name: 'audiotem',
	props: {
		playurl: String,
		isplay: null,
	},
	data () {
		return {
			time: null
		}
	},
	watch: {
		isplay: function(data){
			var that = this;	
			if(data){
				if(this.$refs.audio.readyState==4){
					this.$refs.audio.play()
				}else{
					this.$refs.audio.autoplay = true;
				}
				this.returnPlayTime();
			}else{
				this.$refs.audio.pause();
				this.stopPlayTime();
			}
		}
	},
	methods: {
		returnPlayTime: function(){
			var that = this;	
			that.time = setInterval(function(){
				var time = Math.ceil(that.$refs.audio.currentTime/that.$refs.audio.duration*100) + '%';
				that.$emit('playtime',time)
			},100)
		},
		stopPlayTime: function(){
			clearInterval(this.time)
		}
	}
}
</script>

<style scoped>
.box{
	width: 100%;
	height: 3rem;
	position: fixed;
	bottom: 0;
	left: 0;
	background: #fff;
	border-top: 1px solid #f0f0f0;
}
audio{
	width: 80%;
	height: 2.3rem;
	margin-left: 10%
}
</style>


