import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/page/index'
import Play from '@/page/play'

Vue.use(Router)

export default new Router({
	routes: [
		{
			path: '/',
			name: 'index',
			component: Index,
			meta: {
				title: 'index',
				keepAlive: true
			}
		}, 
		{
			path: '/play',
			name: 'play',
			component: Play
		}
	],
	scrollBehavior (to, from, savedPosition) {
  		if (savedPosition) {
   			return savedPosition
  		} else {
  			if (from.meta.keepAlive) {
  				from.meta.savedPosition = window.scrollTop?window.scrollTop:window.scrollY;
  			}
    		return { x: 0, y: 0 }
  		}
	}
})


